.root {
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.root .highlight {
  background-color: aquamarine;
}

.editor-wrapper {
  border: 1px solid rgb(216, 216, 216);
  padding-left: 10px;
  max-height: 300px;
  overflow: auto;
}

.editor-wrapper textarea:focus-visible {
  outline: none;
}

.save-btn {
  display: inline;
  border-radius: 3px;
  padding: 5px 10px;
  margin-top: 5px;
}

.save-text {
  margin-top: 5px;
}

.text-mode {
  color: #3d87f5;
  float: right;
  margin-top: 5px;
  cursor: pointer;
}

.config-mode__footer {
  display: block;
}